<template>
<div class="couponcard">
  <van-pull-refresh v-model="isRefresh" @refresh="onRefresh(mgChildKey)">
  <van-list
    v-model="loading"
    :finished="finished"
    @load="onLoad(mgChildKey)"
    error-text="请求失败，刷新重新加载"
    loading-text=""
    :immediate-check=false
    :style="{'min-height': (listMinHeight+'px')}"
    >
  <div class="commonCard" v-for="(item,i) in lists" v-if="isLists==false">
    <div class="backPic" :style="{backgroundImage:'url(' + (mgChildKey=='01'?img.waitUsed:(mgChildKey=='02'?img.alreadyUsed:img.outDate)) + ')'}">
    <!--优惠券左侧内容-->
    <div class="leftPoint">
      <div class="couponDetail">
        <div>￥</div>
        <div class="couponTotal">{{item.couponTotal}}</div>
      </div>
      <div class="scopeType"  @click="showScope(i)">
        <span >适用范围</span>
        <img :src="item.ifShowScope==false?img.filterDown:img.filterUp" />
      </div>
    </div>
    <!--优惠券右侧内容-->
    <div class="rightPoint">
      <div class="shopName">{{item.shopName}}</div>
      <div class="couponLimit">{{item.name}}</div>
      <div class="toUse">
      <div class="useDate">{{item.effBeginDay}}-{{item.effEndDay}}</div>
      <div v-if="mgChildKey=='01'" class="toUsebtn" @click="useCoupon(item.shopCode)">去使用</div>
      </div>
    </div>
    </div>
    <div class="scopeInfo" v-if="item.ifShowScope==true">全品类通用</div>
  </div>
    <!--无订单时的显示图片-->
    <div class="noList" :class="{noListThemeL:bindThemeL}" v-if="isLists==true">
      <img :src="img.noList"/>
      <div>暂无优惠券</div>
    </div>
  </van-list>
  </van-pull-refresh>
</div>

</template>

<style scoped="scoped">
  .commonCard{
    margin: 15px 20px;
    /*text-align: center;*/
    /* box-shadow: 0 0 8px 0 rgba(226,226,226,0.50); */

  }
  .backPic{
    height: 100px;
    background-size: 100% 100%;
  }

  .leftPoint{
    width: 25%;
    /* padding-top: 15px; */
    float: left;
    margin: 10px;
  }
  .couponDetail{
    /* float: left; */
    margin-left: 2px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  .scopeType{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: -0.04px;
    margin-top: 10px;
    margin-left: 10px;
  }
  .scopeType img{
    width: 11px;
    height: 6px;
  }
  .couponDetail div:first-child{
    font-family: PingFangSC-Semibold;
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: -0.07px;
    padding-bottom: 5px;
  }
  .couponTotal{
    font-family: PingFangSC-Semibold;
    font-size: 32px;
    color: #FFFFFF;
    letter-spacing: -0.21px;
  }
  .shopName{
    font-family: PingFangSC-Medium;
    font-size: 14px;
    color: #FFFFFF;
    letter-spacing: -0.04px;
    width: 210px;
    white-space: nowrap;overflow: hidden;text-overflow: ellipsis;
    margin-top: 14px;
    /* margin-left: 8px; */
  }
  .couponLimit{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: -0.04px;
    width: 210px;
    white-space: nowrap;overflow: hidden;text-overflow: ellipsis;
  }
  .useDate{
    width: 175px;
    height: 17px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: -0.04px;
    margin-top: 14px;
    float: left;
  }
  .rightPoint{
    float: right;
    width: 67%;
  }
  .toUsebtn{
    width: 66px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    background: #FFFFFF;
    box-shadow: 0 0 15px 0 rgba(207,207,207,0.55);
    border-radius: 15px;
    border: none;
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: #FF9F00;
    letter-spacing: -0.04px;
    margin-right: 15px;
  }
  .toUse{
    display: flex;
    justify-content:space-between;
     align-items:flex-end;
  }
  .active{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: -0.04px;
    float: right;
    margin-right: 22px;
    position: relative;
    top: 60px;
  }
  .scopeInfo{
    height: 25px;
    background: #FFFFFF;
    box-shadow: 0 2px 4px 0 rgba(226,226,226,0.50);
    border-radius: 0px 0px 4px 4px;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #999999;
    letter-spacing: -0.04px;
    padding-left: 20px;
    line-height: 25px;
  }
  .noList img{
    margin:125px 100px 10px 100px;
    width: 180px;
    display:inline-block;
  }
  .noList div{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #9A9A9A;
    letter-spacing: 0.06px;
    text-align: center;
    margin-top: 24px;
  }
  .noListThemeL div{
    font-size: 16px;
  }
  /deep/.van-list__loading {
    display: none;
  }
  /deep/.van-pull-refresh__head {
    position: absolute;
    top: -65px;
    left: 0;
    width: 100%;
    height: 1.33333rem;
    overflow: hidden;
    color: #969799;
    font-size: 0.37333rem;
    line-height: 1.33333rem;
    text-align: center;
  }

</style>

<script>
import {getUserCouponList } from "../../services/orderListService";
import { mapActions, mapState } from "vuex";
    export default
    {
        name:'couponCard',
        props:['mgChildKey','isActKey'],
        data: function(){
            return{
              lists:[],
              page:1,//当前接口请求的页数
              totalPages:5,//订单列表的总页数，先设置一个大于page的初始值
              loading: false,//上拉加载状态
              finished: false,//上拉加载无数据时的状态
              isLists:false,
              scopeType:[],
              isRefresh:false,//是否下拉刷新
              img:{
                waitUsed:require("@/assets/images/mall/mall_bg_coupon_unaccalimed.png"),
                alreadyUsed:require("@/assets/images/mall/bg_coupon_used.png"),
                outDate:require("@/assets/images/mall/bg_coupon_expired.png"),
                filterDown:require("@/assets/images/mall/mall_arrow_down.png"),
                filterUp:require("@/assets/images/mall/mall_arrow_up.png"),
                noList:require("@/assets/images/default/no_coupon_img.png"),//img里面没有找到
              },
              listMinHeight: document.body.clientHeight - this.$stbHeight -118,
              /*大字模式*/
              bindThemeL: this.$curTheme==0?false:true
            }
        },
        methods: {
            getCouponList(type, isRefresh = false){  // 是否刷新
            const _this = this;
            console.log("当前页数"+_this.page+"当前列表"+_this.mgChildKey)
            const data={
              page:_this.page,
              rows:10,
              ownerCode:_this.$userInfo.OWNER_CODE,
              status:_this.mgChildKey,
            }
            getUserCouponList(data).then(function (response) {
              _this.totalPages=response.pages;
              console.log("没有请求数据吗")
              response.data.couponScopeList.map((item)=>{//给每个优惠券添加一个是否显示适用范围的属性值
                item.ifShowScope=false;
              });
              if(response.total==0){
              _this.isLists = true;
              _this.finished = true;
              _this.loading = false;
              _this.isRefresh = false;
              }else{
                _this.lists = isRefresh ? response.data.couponScopeList : _this.lists.concat(response.data.couponScopeList);   
                _this.loading = false;
                _this.isLists = false;
                _this.isRefresh = false;
                console.log("是否结束"+_this.finished);
              }
            });
        },
//          显示适用范围
          showScope(i){
            const _this=this;
            console.log(i);
            const type=_this.lists[i].ifShowScope;
            _this.lists[i].ifShowScope = !type;
          },
          //清空数据
        emptyData(){
          var arr = [];
          arr.push(this.lists[0])
          this.lists = [];
        },
          onLoad(type){
            console.log("onLoad加载数据！");
            const _this=this;
             if(_this.mgChildKey!=_this.isActKey){
            return
          }
            _this.page +=1;
            if(_this.totalPages >= _this.page){
              _this.getCouponList(type, false);
              _this.finished = _this.page > _this.totalPages ? true : false;
            }else if(_this.totalPages < _this.page || _this.lists!=''){
              console.log("总页数小于等于当前页数了");
              // _this.finished=true;
              _this.loading=false;
              _this.isRefresh=false;
            }
          },
          onRefresh(type){
              this.page=1;
              // this.lists=[];
              this.getCouponList(type, true);
          },
          // 立即使用跳转店铺
          useCoupon(shopCode){
             this.$router.push({name: 'shopPage',params:{shopCode:shopCode}});
          }
      },
    }
</script>
