<template>
  <div class="myOrderList">

    <!-- <van-nav-bar class="title" title="优惠券" @click-left="goToBack" left-arrow :style="setTopHeight"></van-nav-bar> -->
     <navigation-bar class="title" @onLeftClick="goToBack()" pageName="我的优惠券" :style="setTopHeight"/>
    <div class="tabTitle" :class="{themeL:bindThemeL}">
      <van-tabs v-model="active" color="#17B7BD"  @click="controlTab" line-width="0.67rem" line-height="0.08rem">
        <van-tab  v-for="(tit,i) in tabTitle" :title="tit" :key="i">
        </van-tab>
      </van-tabs>
    </div>
    <!-- <van-pull-refresh v-model="isPushLoading" @refresh="onRefresh"> -->
      <van-swipe class="orderSwipe" :show-indicators="false" :initial-swipe="0" :loop="false" @change="controlSwipe" ref="swipe" >

        <van-swipe-item  :key="0">
          <!-- <span :style="{'visibility':'hidden'}">.</span> -->
          <couponCard ref="mychild0" mgChildKey="01" :isActKey="'0'+(active+1)">

          </couponCard>
        </van-swipe-item>

        <van-swipe-item  :key="1">
          <!-- <span :style="{'visibility':'hidden'}">.</span> -->
          <couponCard  ref="mychild1" mgChildKey="02" :isActKey="'0'+(active+1)">

          </couponCard>
        </van-swipe-item>
        <van-swipe-item  :key="2">
          <!-- <span :style="{'visibility':'hidden'}">.</span> -->
          <couponCard ref="mychild2" mgChildKey="03" :isActKey="'0'+(active+1)">

          </couponCard>
        </van-swipe-item>
      </van-swipe>
    <!-- </van-pull-refresh> -->
  </div>

</template> 

<style scoped lang="scss">
  .myOrderList {
    width: 100% ;
    background: #f9f9f9 ;
    height: 100% ;
    display:flex;
    flex-direction:column;
    /* overflow:auto; */
  }
  .myOrderList .title{
    z-index: 100;
    width: 100%;
  }
  .myOrderList .commonHead{
    width: 25px;
    padding:20px 20px 10px 20px;
    float: left;
  }
  .myOrderList .orderTitle{
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #333333;
    letter-spacing: -0.06px;
    float: right;
    padding-top: 20px;
    padding-right: 167px;
  }
  .tabTitle{
    width: 100%;
    /* z-index: 100; */
    background: #FFFFFF;
    /* margin-top: 1px; */
  }
  .orderSwipe{
    /* margin-top: 66px; */
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    /* margin-bottom: 10px; */
  }
  /deep/.van-tabs {
    position: relative;
    padding: 0px 50px;
    margin-top: 0px;
    line-height: 60px!important;
  }
   /deep/.van-tab {
     line-height: 52px!important;
   }
   /deep/.themeL .van-tab {
     font-size: 17px!important;
   }
  /deep/.van-hairline--top-bottom {
    height: 1.33333rem !important;
    line-height: 1.33333rem !important;
    border:none !important;
}
/deep/.van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after{
  border-width: 0 0;
}
/deep/.van-tabs__line {
    position: absolute;
    bottom: 0.5rem!important;
    left: 0;
    z-index: 1;
    height: 0.08rem;
    background-color: #ee0a24;
    border-radius: 0.08rem;
}
</style>

<script>

  import couponCard from './couponCard.vue';
  import NavigationBar from '@/common/currency/NavigationBar.vue';
    export default{
        data:function(){
            return{
              tabTitle:['待使用','已使用','已过期'],
              active:0,
              key:0,
              isPushLoading:false,//下拉刷新
              setTopHeight:{
                'padding-top':this.$stbHeight+'px'
                },
              /*大字模式*/
              bindThemeL: this.$curTheme==0?false:true
            }
        },
        components:{couponCard,NavigationBar},
        methods: {
          controlTab(index){
            const _this=this;          
            _this.key=index;        //同步swipe和tabbar的选中状态
            _this.$refs.swipe.swipeTo(index);
            window.scrollTo(0,0);//切换swipe时回到页面顶端
          },
          controlSwipe(index){
             const _this=this;
             _this.active=index;
            if(index=='0'){
              this.$refs.mychild0.onRefresh('1');
              this.$refs.mychild1.emptyData();
              this.$refs.mychild2.emptyData();
            }else if(index=='1'){
               this.$refs.mychild1.onRefresh('2');
               this.$refs.mychild0.emptyData();
                this.$refs.mychild2.emptyData();
            }else{
              this.$refs.mychild2.onRefresh('3');
              this.$refs.mychild1.emptyData();
              this.$refs.mychild0.emptyData();
            }
          },
          goToBack(){
            let backResult=this.$setRouterIndex();
            if(backResult){
              this.$router.go(-1);
            }
          }
        },
        mounted:function(){
          this.controlSwipe('0');
        }
    }
</script>
